
import { history } from "../../../history";
import { subRooter } from "../../../configs/vars";
import Config from "../../../configs/config";
import {Axios} from "../root";
import { LOGIN_WITH_JWT, LOGOUT_WITH_JWT } from "../types";
import { displayServerErrorMessage, displaySuccessMessage } from "./../../../service/Alert";

export const changeRole = role => {
  return dispatch => dispatch({ type: "CHANGE_ROLE", userRole: role })
}

export const Logout = () => {
  return dispatch => {
    localStorage.removeItem(Config.token);
    dispatch({ type: LOGOUT_WITH_JWT, payload: {} })
    window.location.reload(true);
  }
}

export const is_session = () => {
  if(localStorage[Config.token] && localStorage[Config.token] !== "undefined"){
    return true;
  }else{
    return false;
  }
}

export const loginWithToken = () => {
  // let token = localStorage.getItem(Config.token);
  return dispatch => {
    Axios
      .post('/signin-with-token')
      .then((res) => {
        if (res.data) {
          let loggedInUser = res.data;
          localStorage.setItem(Config.token, loggedInUser.token);

          dispatch({
            type: LOGIN_WITH_JWT,
            payload: { loggedInUser, loggedInWith: "jwt" },
            isLoggedIn: true
          })
        }
      })
      .catch((err) => {
        console.error(err)
        history.push(`${subRooter}/login`)
      })
  }
}

export const signinWithJWT = user => {
  return dispatch => {
    Axios
      .post("/signin", {
        email: user.email,
        password: user.password
      })
      .then(response => {
        if (response.data) {
          let loggedInUser = response.data;
          localStorage.setItem(Config.token, loggedInUser.token);

          dispatch({
            type: LOGIN_WITH_JWT,
            payload: { loggedInUser, loggedInWith: "jwt" },
            isLoggedIn: true
          })  
          history.push("/");
          displaySuccessMessage("Login Successfully");
          window.location.reload(true)
        }
      })
      .catch((err) => {
        console.error(err)
        history.push(`${subRooter}/login`);
        displayServerErrorMessage(err)
      })
  }
}
