import * as Types from '../../actions/types';

const initialState = {
  data: [],
  params: null,
  allData: [],
  totalPages: 0,
  filteredData: [],
  totalRecords: 0,
  sortIndex: [],
  dataPerType: []
}

// const moveIndex = (arr, from, to) => {
//   let el = arr[from]
//   arr.splice(from, 1)
//   arr.splice(to, 0, el)
// }

const getIndex = (arr, arr2, arr3, params = {}) => {
  if (arr2.length > 0) {
    let startIndex = arr.findIndex(i => i._id === arr2[0]._id) + 1
    let endIndex = arr.findIndex(i => i._id === arr2[arr2.length - 1]._id) + 1
    let finalArr = [startIndex, endIndex]
    return (arr3 = finalArr)
  } else {
    let finalArr = [arr.length - parseInt(params.perPage), arr.length]
    return (arr3 = finalArr)
  }
}

const TopicReducer = (state = initialState, action) => {
  switch (action.type) {
    case Types.FETCH_TOPICS:
      return {
        ...state,
        data: action.data ,
        totalPages: action.totalPages,
        params: action.params,
        sortIndex: getIndex(
          state.allData,
          action.data,
          state.sortIndex,
          action.params
        )
      }
    case Types.FETCH_TOPICS_BY_TYPE:
      return {
        ...state,
        dataPerType: action.data,
      }
    case Types.FETCH_ALL_TOPIC:
      return {
        ...state,
        allData: action.data,
        totalRecords: action.data.length,
        sortIndex: getIndex(action.data, state.data, state.sortIndex)
      }
    case Types.FILTER_TOPIC:  
      let value = action.value
      let filteredData = [];

      if (value.length) {
        filteredData = state.allData
          .filter(item => {
            if(item.createdBy) {
              item.createdBy.name = item.createdBy.firstname + " " + item.createdBy.lastname
            }

            let startsWithCondition =
              item.name.toLowerCase().startsWith(value.toLowerCase()) || 
              (item.levelId && item.levelId.name.toLowerCase().startsWith(value.toLowerCase())) || 
              (item.categoryId && item.categoryId.name.toLowerCase().startsWith(value.toLowerCase())) ||
              item.type.toLowerCase().startsWith(value.toLowerCase()) || 
              (item.createdBy && item.createdBy.firstname.toLowerCase().startsWith(value.toLowerCase())) ||
              (item.createdAt && item.createdAt.toLowerCase().startsWith(value.toLowerCase())) ||
              (item.updatedAt && item.updatedAt.toLowerCase().startsWith(value.toLowerCase()))


            let includesCondition =
              item.name.toLowerCase().includes(value.toLowerCase())  ||
              (item.levelId && item.levelId.name.includes(value.toLowerCase())) ||
              (item.categoryId && item.categoryId.name.includes(value.toLowerCase())) ||
              item.type.toLowerCase().startsWith(value.toLowerCase()) || 
              (item.createdBy && item.createdBy.name.includes(value.toLowerCase())) ||
              (item.createdAt && item.createdAt.includes(value.toLowerCase())) ||
              (item.updatedAt && item.updatedAt.includes(value.toLowerCase()))

            if (startsWithCondition) {
              return startsWithCondition
            } else if (!startsWithCondition && includesCondition) {
              return includesCondition
            } else return null
          })
          .slice(state.params.page - 1, state.params.perPage)
        return { ...state, filteredData }
      } else {
        filteredData = state.data
        return { ...state, filteredData }
      }
    // case Types.ADD_TOPIC:
    //   let id = state.data.slice(-1)[0]._id + 1
    //   state.data.push({
    //     ...action.obj,
    //   })
    //   moveIndex(
    //     state.data,
    //     state.data.findIndex(item => item._id === id),
    //     0
    //   )
    //   return {
    //     ...state,
    //     data: state.data,
    //     totalRecords: state.allData.length,
    //     sortIndex: getIndex(state.allData, state.data, state.sortIndex)
    //   }
    case Types.UPDATE_TOPIC:
      state.data.find(item => {
        if (item._id === action.obj._id) {
          return Object.assign(item, { ...action.obj})
        } else {
          return item
        }
      })
      return { ...state }
    case Types.DELETE_TOPIC:
      let updatedData = [...state.data].filter((elt) => elt._id !== action.obj._id);
      return {
        ...state,
        data: updatedData,
        totalRecords: state.allData.length,
        sortIndex: getIndex(
          state.allData,
          state.data,
          state.sortIndex,
          state.params
        )
      }
    case Types.DELETE_TOPICS: 
      let deletedIds = action.ids;
      let changedData = [...state.data].filter((elt) => {
        return deletedIds.find((id) => id === elt._id)
      });
      return {
        ...state,
        data: changedData,
        totalRecords: state.allData.length,
        sortIndex: getIndex(
          state.allData,
          state.data,
          state.sortIndex,
          state.params
        )
      }
    default:
      return state
  }
}

export default TopicReducer
