import * as Types from '../../actions/types';

const initialState = {
  data: [],
  params: null,
  allData: [],
  totalPages: 0,
  filteredData: [],
  totalRecords: 0,
  sortIndex: [],
  queryData: [],
  myAssessments: []
}

const getIndex = (arr, arr2, arr3, params = {}) => {
  if (arr2.length > 0) {
    let startIndex = arr.findIndex(i => i._id === arr2[0]._id) + 1
    let endIndex = arr.findIndex(i => i._id === arr2[arr2.length - 1]._id) + 1
    let finalArr = [startIndex, endIndex]
    return (arr3 = finalArr)
  } else {
    let finalArr = [arr.length - parseInt(params.perPage), arr.length]
    return (arr3 = finalArr)
  }
}

const AssessmentReducer = (state = initialState, action) => {
  switch (action.type) {
    case Types.FETCH_ASSESSMENTS:
      return {
        ...state,
        data: action.data,
        totalPages: action.totalPages,
        params: action.params,
        sortIndex: getIndex(
          state.allData,
          action.data,
          state.sortIndex,
          action.params
        )
      }
    case Types.FETCH_ALL_ASSESSMENTS:
      return {
        ...state,
        allData: action.data,
        totalRecords: action.data.length,
        sortIndex: getIndex(action.data, state.data, state.sortIndex)
      }
    case Types.FILTER_ASSESSMENT:  
      let value = action.value;
      let filteredData = [];
      // let filterOption = action.filterOption;

      // let rangePicker = filterOption.rangePicker;
      // let fromTime = 0
      // let endTime = new Date().getTime();

      // if( rangePicker && rangePicker[0] ) {
      //   fromTime = new Date(rangePicker[0]).getTime()
      // }
      // if( rangePicker && rangePicker[1] ) {
      //   endTime = new Date(rangePicker[1]).getTime()
      // }
    
      filteredData = state.allData
        .filter(item => {
          if(item.createdBy) {
            item.createdBy.name = item.createdBy.firstname + " " + item.createdBy.lastname
          }

          let startsWithCondition =
            item.question.toLowerCase().startsWith(value.toLowerCase()) ||
            item.topic.name.toLowerCase().startsWith(value.toLowerCase()) ||
            item.level.name.toLowerCase().startsWith(value.toLowerCase()) ||
            item.createdBy.name.toLowerCase().startsWith(value.toLowerCase()) ||
            item.topic.categoryId.name.toLowerCase().startsWith(value.toLowerCase())

          let includesCondition =
            item.question.toLowerCase().includes(value.toLowerCase())
            item.topic.name.toLowerCase().includes(value.toLowerCase()) ||
            item.level.name.toLowerCase().includes(value.toLowerCase()) ||
            item.createdBy.name.toLowerCase().includes(value.toLowerCase()) ||
            item.topic.categoryId.name.toLowerCase().includes(value.toLowerCase())


          if (startsWithCondition) {
            return startsWithCondition
          } else if (!startsWithCondition && includesCondition) {
            return includesCondition
          } else return null
        })
        // .filter((item) => {
        //   let condition = (item.isMCQ === filterOption.isMCQ)
        //   //  && ( item.level && item.level._id === filterOption.selectedLevelId || filterOption.selectedLevelId === "" )
        //   //  && ( item.topic._id === filterOption.selectedTopicId ||  filterOption.selectedTopicId === "")
        //   //  && ( item.topic.categoryId._id === filterOption.selectedCategoryId || filterOption.selectedCategoryId === "")
        //   //  && ( new Date(item.createdAt).getTime() > fromTime && new Date(item.createdAt).getTime() < endTime)
        //   console.log("item.isMCQ", item.isMCQ);
        //   console.log("filterOption.isMCQ", filterOption.isMCQ);
        //   return condition
        // })
        .slice(state.params.page - 1, state.params.perPage)
      return { ...state, filteredData }

    case Types.ADD_ASSESSMENT:
      // state.data.push({
      //   ...action.obj,
      // })
      return {
        ...state,
        data: state.data,
        totalRecords: state.allData.length,
        sortIndex: getIndex(state.allData, state.data, state.sortIndex)
      }
    case Types.UPDATE_ASSESSMENT:
      state.data.find(item => {
        if (item._id === action.obj._id) {
          return Object.assign(item, { ...action.obj})
        } else {
          return item
        }
      })
      return { ...state }
    case Types.DELETE_ASSESSMENT:
      let index = state.data.findIndex(item => item._id === action.obj._id)
      let updatedData = [...state.data]
      updatedData.splice(index, 1)
      return {
        ...state,
        data: updatedData,
        totalRecords: state.allData.length,
        sortIndex: getIndex(
          state.allData,
          state.data,
          state.sortIndex,
          state.params
        )
      }
    case Types.DELETE_ASSESSMENTS:
      let ids = action.ids;
      let allData = state.allData.filter((elt) => ! ids.includes(elt._id))
      let data = state.data.filter((elt) => ! ids.includes(elt._id))
      return {
        ...state,
        data: data,
        totalRecords: allData.length,
        allData
      }
    case Types.FILTER_ASSESSMENT_BY_QUERY:
      return {
        ...state,
        queryData: action.data
      }
    case Types.FETCH_ALL_MY_ASSESSMENT:
      return {
        ...state,
        myAssessments: action.data
      }
    default:
      return state
  }
}

export default AssessmentReducer
