import * as Types from '../../actions/types';

const initialState = {
  parentData: [],
  allData: [],
}

const categoryReducer = (state = initialState , action) => {
  switch (action.type) {
    case Types.ADD_CATEGORY:
      state.allData.push(action.data)
      return {...state, parentData: state.parentData.slice(), allData: state.allData.slice()};

    case Types.FETCH_ALL_CATEGORIES:
      return {...state, allData: action.data.slice()};

    case Types.FETCH_ALL_CATEGORY_PARENT:
      return {...state, parentData: action.data.slice()};

    case Types.DELETE_CATEGORY:
      if(action.data.isParent) {
        state.parentData = state.parentData.filter((elt) => elt._id !== action.data._id)
      }
      state.allData = state.allData.filter((elt) => elt._id !== action.data._id)
      return {...state, parentData: state.parentData.slice(), allData: state.allData.slice()};

    case Types.UPDATE_CATEGORY:
      return state;
      
    default:
      return state
  }
}

export default categoryReducer