import { FETCH_TEACHERS, FETCH_MY_STUDENTS } from '../../actions/types';

const initialObject =  {
  tutors: [],
  mystudents: [],
}

const users = ( state = initialObject, action) => {
  switch (action.type) {
    case FETCH_TEACHERS: {
      return { ...state, tutors: action.payload }
    }
    case FETCH_MY_STUDENTS: {
      return { ...state, mystudents: action.payload }
    }
    // case "CHANGE_ROLE": {
    //   return { ...state, userRole: action.userRole }
    // }
    default: {
      return state
    }
  }
}


export default users