import * as Types from '../../actions/types';

const initialState = {
  levelId: "",
  categoryId: "",
  topicId: "",
}

const selectTopicReducer = (state = initialState, action) => {
  switch (action.type) {
    case Types.SET_SELECT_TOPIC_FORM:
      return {
        ...state,
        levelId: action.levelId,
        categoryId: action.categoryId,
        topicId: action.topicId,
      }
    default:
      return state
  }
}

export default selectTopicReducer
