import { toast } from 'react-toastify';

export const displaySuccessMessage = (message) => {
  toast.success(message, {
    position: "bottom-right",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
  });
}

export const displayErrorMessage = (message) => {
  toast.error(message, {
    position: 'bottom-right',
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
  });
}

export const displayServerErrorMessage = (err) => {
  var message = "";
  if(err && err.response && err.response.data && err.response.data.error) {
    message = err.response.data.error.toString()
  } else if(err && err.response && err.response.data) {
    message = err.response.data.toString()
  } else if(err && err.response) {
    message = err.response.toString()
  } else {
    message = err.toString()
  }
  
  toast.error(message, {
    position: 'bottom-right',
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
  });
}


