import * as Types from '../../actions/types';

const initialState = {
  answers: [],
  assignId: '',
  assessmentId: '',
}

const answerReducer = (state = initialState, action) => {
  switch (action.type) {
    case Types.FETCH_ASSIGN_ANSWER:
      return {
        ...state,
        answers: action.obj,
        assessmentId: action.assessmentId,
        assignId: action.assignId,
      }
    default:
      return state
  }
}

export default answerReducer
