import * as Types from '../../actions/types';

const initialState = {
  data: [],
  params: null,
  allData: [],
  totalPages: 0,
  filteredData: [],
  totalRecords: 0,
  sortIndex: [],
  currentWorkSheet: {}
}


const getIndex = (arr, arr2, arr3, params = {}) => {
  if (arr2.length > 0) {
    let startIndex = arr.findIndex(i => i._id === arr2[0]._id) + 1
    let endIndex = arr.findIndex(i => i._id === arr2[arr2.length - 1]._id) + 1
    let finalArr = [startIndex, endIndex]
    return (arr3 = finalArr)
  } else {
    let finalArr = [arr.length - parseInt(params.perPage), arr.length]
    return (arr3 = finalArr)
  }
}

const WorkSheetReducer = (state = initialState, action) => {
  switch (action.type) {
    case Types.FETCH_WORKSHEETS:
      return {
        ...state,
        data: action.data,
        totalPages: action.totalPages,
        params: action.params,
        sortIndex: getIndex(
          state.allData,
          action.data,
          state.sortIndex,
          action.params
        )
      }
    case Types.FETCH_WORKSHEET_BY_ID:
      return {
        ...state,
        currentWorkSheet: action.data
      }
    case Types.FETCH_ALL_WORKSHEETS:
      return {
        ...state,
        allData: action.data,
        totalRecords: action.data.length,
        sortIndex: getIndex(action.data, state.data, state.sortIndex)
      }
    case Types.FILTER_WORKSHEET:
      let value = action.value
      let filteredData = [];

      if (value.length) {
        filteredData = state.allData
          .filter(item => {
            if(item.createdBy) {
              item.createdBy.name = item.createdBy.firstname + " " + item.createdBy.lastname
            }

            let startsWithCondition =
              item.name.toLowerCase().startsWith(value.toLowerCase())


            let includesCondition =
              item.name.toLowerCase().includes(value.toLowerCase())

            if (startsWithCondition) {
              return startsWithCondition
            } else if (!startsWithCondition && includesCondition) {
              return includesCondition
            } else return null
          })
          .slice(state.params.page - 1, state.params.perPage)
        return { ...state, filteredData }
      } else {
        filteredData = state.data
        return { ...state, filteredData }
      }
    case Types.ADD_WORKSHEET:
      state.data.push({
        ...action.obj,
      })
      return {
        ...state,
        data: state.data,
        totalRecords: state.allData.length,
        sortIndex: getIndex(state.allData, state.data, state.sortIndex)
      }
    case Types.UPDATE_WORKSHEET:
      state.data.find(item => {
        if (item._id === action.obj._id) {
          return Object.assign(item, { ...action.obj})
        } else {
          return item
        }
      })
      return { ...state }
    case Types.DELETE_WORKSHEET:
      let index = state.data.findIndex(item => item._id === action.obj._id)
      let updatedData = [...state.data]
      updatedData.splice(index, 1)
      return {
        ...state,
        data: updatedData,
        totalRecords: state.allData.length,
        sortIndex: getIndex(
          state.allData,
          state.data,
          state.sortIndex,
          state.params
        )
      }
    case Types.DELETE_WORKSHEETS:
      let ids = action.ids;
      let allData = state.allData.filter((elt) => ! ids.includes(elt._id))
      let data = state.data.filter((elt) => ! ids.includes(elt._id))
      return {
        ...state,
        data: data,
        totalRecords: allData.length,
        allData
      }
    default:
      return state
  }
}

export default WorkSheetReducer
