import * as Types from '../../actions/types';

const initialState = {
  allData: [],
  filteredData: [],
  filteredList: [],
}

const AssignReducer = (state = initialState, action) => {
  let allData, filteredData, filteredList;

  switch (action.type) {
    case Types.ADD_ASSIGN:
      allData = state.allData;
      allData.push(action.obj)
      return {
        ...state,
        allData: allData
      }
    case Types.UPDATE_ASSIGN: 
      state.allData.find(item => {
        if (item._id === action.obj._id) {
          return Object.assign(item, { ...action.obj})
        } else {
          return item
        }
      })
      state.filteredData.find(item => {
        if (item._id === action.obj._id) {
          return Object.assign(item, { ...action.obj})
        } else {
          return item
        }
      })
      state.filteredList.find(item => {
        if (item._id === action.obj._id) {
          return Object.assign(item, { ...action.obj})
        } else {
          return item
        }
      })
      return {
        ...state,
      }
    case Types.FETCH_ASSIGNS_BY_FILTER:
      return {
        ...state,
        filteredList: action.data
      }
    case Types.DELETE_ASSIGN:
      allData = state.allData.filter((elt) => elt._id !== action.obj._id);
      filteredData = state.filteredData.filter((elt) => elt._id !== action.obj._id);
      filteredList = state.filteredList.filter((elt) => elt._id !== action.obj._id);
      return {
        ...state,
        allData,
        filteredData,
        filteredList,
      }
    default:
      return state
  }
}

export default AssignReducer
