import * as Types from '../../actions/types';

const initialState = {
  data: [],
  params: null,
  allData: [],
  totalPages: 0,
  filteredData: [],
  totalRecords: 0,
  sortIndex: []
}

const moveIndex = (arr, from, to) => {
  let el = arr[from]
  arr.splice(from, 1)
  arr.splice(to, 0, el)
}

const getIndex = (arr, arr2, arr3, params = {}) => {
  if (arr2.length > 0) {
    let startIndex = arr.findIndex(i => i._id === arr2[0]._id) + 1
    let endIndex = arr.findIndex(i => i._id === arr2[arr2.length - 1]._id) + 1
    let finalArr = [startIndex, endIndex]
    return (arr3 = finalArr)
  } else {
    let finalArr = [arr.length - parseInt(params.perPage), arr.length]
    return (arr3 = finalArr)
  }
}

const levelReducer = (state = initialState , action) => {
  switch (action.type) {
    case Types.FETCH_LEVELS:
      return {
        ...state,
        data: action.data,
        totalPages: action.totalPages,
        params: action.params,
        sortIndex: getIndex(
          state.allData,
          action.data,
          state.sortIndex,
          action.params
        )
      }
    case Types.ADD_LEVEL:
      state.data.push({
        ...action.obj,
      })
      moveIndex(
        state.data,
        state.data.findIndex(item => item._id === action.obj._id),
        0
      )
      return {
        ...state,
        data: state.data,
        totalRecords: state.allData.length,
        sortIndex: getIndex(state.allData, state.data, state.sortIndex)
      }
    case Types.FETCH_ALL_LEVEL:
      return {
        ...state,
        allData: action.data,
        totalRecords: action.data.length,
        sortIndex: getIndex(action.data, state.data, state.sortIndex)
      }
    case Types.DELETE_LEVEL:
      let index = state.data.findIndex(item => item._id === action.obj._id)
      let updatedData = [...state.data]
      updatedData.splice(index, 1)
      return {
        ...state,
        data: updatedData,
        totalRecords: state.allData.length,
        sortIndex: getIndex(
          state.allData,
          state.data,
          state.sortIndex,
          state.params
        )
      }
    case Types.UPDATE_LEVEL:
      state.data.find(item => {
        if (item._id === action.obj._id) {
          return Object.assign(item, { ...action.obj })
        } else {
          return item
        }
      });
      return { ...state }
    case Types.FILTER_LEVEL:
      let value = action.value
      let filteredData = []
      if (value.length) {
        filteredData = state.allData
          .filter(item => {
            let startsWithCondition =
              item.name.toLowerCase().startsWith(value.toLowerCase()) ||
              item.description.toLowerCase().startsWith(value.toLowerCase())/* ||
              item.category.toLowerCase().startsWith(value.toLowerCase()) ||
              item.price.toLowerCase().startsWith(value.toLowerCase()) ||
              item.order_status.toLowerCase().startsWith(value.toLowerCase()) */

            let includesCondition =
            item.name.toLowerCase().includes(value.toLowerCase()) ||
            item.description.toLowerCase().includes(value.toLowerCase()) /* ||
            item.category.toLowerCase().includes(value.toLowerCase()) ||
              item.price.toLowerCase().includes(value.toLowerCase()) ||
              item.order_status.toLowerCase().includes(value.toLowerCase())
              */

            if (startsWithCondition) {
              return startsWithCondition
            } else if (!startsWithCondition && includesCondition) {
              return includesCondition
            } else return null
          })
          .slice(state.params.page - 1, state.params.perPage)
        return { ...state, filteredData }
      } else {
        filteredData = state.data
        return { ...state, filteredData }
      }
    case Types.DELETE_LEVELS:
      let ids = action.ids;
      let changedData = [...state.data]
      changedData = changedData.filter((elt) =>{
        return ids.find((id) => (id !== elt._id))
      })
      return {
        ...state,
        data: changedData,
        totalRecords: state.allData.length,
        sortIndex: getIndex(
          state.allData,
          state.data,
          state.sortIndex,
          state.params
        )
      }
    default: {
      return state
    }
  }
}


export default levelReducer