import { combineReducers } from "redux"
import customizer from "./customizer/"
import auth from "./auth"
import navbar from "./navbar/Index"

import chatReducer from "./chat/"
import todoReducer from "./todo/"
import emailReducer from "./email/"
import dataListReducer from "./data-list/"
import levelReducer from "./level/"
import topicReducer from "./topic"
import categoryReducer from "./category/"
import lessonReducer from "./lesson/"
import assessment from "./assessment/"
import usersReducer from "./user/"
import worksheetReducer from "./worksheet/"
import assignedReducer from "./assigned/"
import topicWrapperReducer from "./selectTopic/"
import answerReducer from "./answer/"

const rootReducer = combineReducers({
  customizer: customizer,
  auth: auth,
  chatApp: chatReducer,
  todoApp: todoReducer,
  emailApp: emailReducer,
  dataList: dataListReducer,
  navbar: navbar,
  level: levelReducer,
  category: categoryReducer,
  topic: topicReducer,
  lesson: lessonReducer,
  assessment,
  users: usersReducer,
  worksheet: worksheetReducer,
  assigned: assignedReducer,
  topicSelectForm: topicWrapperReducer,
  answer: answerReducer,
})

export default rootReducer
